<template>
  <div id="app">
    <Header></Header>
    <div class="main">
      <router-view name="slidebar"></router-view>
      <router-view name="main"></router-view>
    </div>
  </div>
</template>

<script>
import Header from "./components/Header";
import PostList from "./components/PostList";
export default {
  name: "App",
  components: {
    Header,
    PostList
  }
};
</script>

<style>
.loading img {
  width: 30px;
  height: 30px;
  margin: 20% 0 0 38%;
}
* {
  padding: 0;
  margin: 0;
  user-select: none;
}
body {
  background-color: #e1e1e1;
}
.main {
  margin: 15px 0 40px 13%;
}
</style>



