<template>
  <div class="UserInfo">
    <!-- 在数据未返回的时候，显示这个正在加载的gif -->
    <div class="loading" v-if="isLoading">
      <img src="../assets/timg.gif" />
    </div>
    <div class="userinfomation" v-else>
      <section>
        <div class="homepage">
          <router-link :to="{name:'root'}">主页</router-link>
        </div>
        <img :src="userinfo.avatar_url" />
        <span>{{userinfo.loginname}}</span>
        <p>{{userinfo.score}} 积分</p>
        <p class="times">注册时间： {{userinfo.create_at | formatDate}}</p>
      </section>
      <div class="topics">
        <p>最近创建的话题</p>
        <ul>
          <li v-for="item in topcilimitby5">
            <!-- 头像 -->
            <img :src="item.author.avatar_url" />
            <!-- 标题 -->
            <router-link :to="{name:'post_content',params:{id:item.id}}">{{item.title}}</router-link>
            <!-- 创建时间 -->
            <span class="last_reply">{{item.last_reply_at | formatDate}}</span>
          </li>
        </ul>
      </div>
      <div class="replies">
        <p>最近回复的话题</p>
        <ul>
          <li v-for="item in replieilimitby5">
            <img :src="item.author.avatar_url" />
            <router-link :to="{name:'post_content',params:{id:item.id}}">{{item.title}}</router-link>
            <!-- 参与时间 -->
            <span class="last_reply">{{item.last_reply_at | formatDate}}</span>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "UserInfo",
  data() {
    return {
      isLoading: false,
      userinfo: {}
    };
  },
  methods: {
    getDate() {
      this.$http
        .get(`https://cnodejs.org/api/v1/user/${this.$route.params.name}`)
        .then(res => {
          this.isLoading = false; //加载成功去除动画
          this.userinfo = res.data.data;
        })
        .catch(function(err) {
          console.log(err);
        });
    }
  },
  computed: {
    topcilimitby5() {
      if (this.userinfo.recent_topics) {
        return this.userinfo.recent_topics.slice(0, 5);
      }
    },
    replieilimitby5() {
      if (this.userinfo.recent_replies) {
        return this.userinfo.recent_replies.slice(0, 5);
      }
    }
  },
  beforeMount() {
    this.isLoading = true; //在页面加载成功之前获取动画
    this.getDate(); // 在页面加载之前获取数据
  }
};
</script>
<style scoped>
.homepage {
  height: 40px;
  background-color: #f6f6f6;
  line-height: 40px;
  padding-left: 10px;
  border-radius: 5px 5px 0 0;
}
.homepage a {
  text-decoration: none;
  color: #80bd01;
}
.homepage a:hover {
  text-decoration: underline;
}
section {
  border-radius: 5px;
  box-sizing: border-box;
  width: 1100px;
  background-color: #ffffff;
  color: #778087;
  font-size: 14px;
  position: relative;
}
section img {
  width: 40px;
  height: 40px;
  padding: 10px;
}
section span {
  position: absolute;
  top: 55px;
}
section p {
  padding: 0 0 10px 10px;
}
section p {
  color: #333333;
}
.times {
  color: #ababab;
}
.topics,
.replies {
  border-radius: 5px;
  box-sizing: border-box;
  width: 1100px;
  background-color: #ffffff;
  color: #444444;
  font-size: 14px;
  margin-top: 10px;
}
.topics p,
.replies p {
  border-radius: 5px 5px 0 0;
  height: 40px;
  background-color: #f6f6f6;
  line-height: 40px;
  padding-left: 20px;
}
ul {
  list-style-type: none;
}
li {
  height: 50px;
  border-top: 1px solid #f0f0f0;
  line-height: 50px;
  position: relative;
}
li img {
  position: absolute;
  width: 30px;
  height: 30px;
  top: 10px;
  left: 10px;
}
li a {
  display: inline-block;
  color: #00a5e2;
  text-decoration: none;
  font-size: 15px;
  margin-left: 50px;
}
li a:hover {
  text-decoration: underline;
  color: #007ab3;
}
.last_reply {
  float: right;
  font-size: 12px;
  margin-right: 10px;
  color: #778087;
}
</style>


