<template>
  <div class="article">
    <div class="loading" v-if="isLoading">
      <img src="../assets/timg.gif" />
    </div>
    <div v-else>
      <div class="topic_header">
        <div class="topic_title">{{post.title}}</div>
        <ul>
          <li>• 发布于：{{post.create_at | formatDate}}</li>
          <li>• 作者：{{post.author.loginname}}</li>
          <li>• {{post.visit_count}}次浏览</li>
          <li>• 来自 {{post | tabFormatter}}</li>
        </ul>
        <hr />
        <div v-html="post.content" class="topic_content"></div>
      </div>
      <div id="reply">
        <div class="topbar-a">回复</div>
        <div v-for="(reply,index) in post.replies" class="replySec" :key="reply.index">
          <div class="replyUp">
            <router-link
              :to="{
          name:'user_info',
          params:{
            name:reply.author.loginname
          }
          }"
            >
              <img :src="reply.author.avatar_url" class="little-head" />
            </router-link>
            <router-link
              :to="{
          name:'user_info',
          params:{
            name:reply.author.loginname
          }
          }"
            >
              <span class="wwfName">{{reply.author.loginname}}</span>
            </router-link>
            <span class="floor">{{index+1}}楼</span>
          </div>
          <p v-html="reply.content"></p>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "Article",
  data() {
    return {
      isLoading: false,
      post: {}
    };
  },
  methods: {
    getArticleDate() {
      this.$http
        .get(`https://cnodejs.org/api/v1/topic/${this.$route.params.id}`)
        .then(res => {
          if (res.data.success == true) {
            this.isLoading = false;
            this.post = res.data.data;
          }
        })
        .catch(function(err) {
          console.warn(err);
        });
    }
  },
  beforeMount() {
    this.isLoading = true;
    this.getArticleDate();
  },
  watch: {
    $route(to, from) {
      this.getArticleDate();
    }
  }
};
</script>
<style >
@import "../assets/markdown-github.css";
.article ul {
  list-style-type: none;
  margin: 0px 0px 10px 10px;
}
.topic_header {
  width: 1100px;
  background-color: #ffffff;
  border-radius: 5px;
}
.topic_title {
  width: 800px;
  font-size: 22px;
  font-weight: bold;
  padding: 20px 0 10px 10px;
  color: #333333;
}
.article ul li {
  display: inline-block;
  font-size: 12px;
  color: #838383;
  margin-left: 8px;
}
.topic_content {
  padding: 5px 20px 10px;
  font-size: 15px;
  color: #333333;
}
.topic_content p {
  margin: 15px 0;
  line-height: 1.7em;
}
.markdown-text img {
  width:80% !important;
}
.topic_content a {
  text-decoration: none;
  color: #0088d5;
}
.topic_content a:hover {
  text-decoration: underline;
}
#reply {
  margin-top: 10px;
  width: 1100px;
  border-radius: 5px;
  background-color: #ffffff;
}
.topbar-a {
  border-radius: 5px 5px 0 0;
  height: 40px;
  background-color: #f6f6f6;
  color: #444444;
  font-size: 14px;
  padding: 10px 0px 0px 10px;
  box-sizing: border-box;
}
.replySec {
  width: 1100px;
  padding: 10px 0 0 10px;
  border-bottom: 1px solid #f0f0f0;
  box-sizing: border-box;
}
.little-head {
  width: 30px;
  height: 30px;
}
.replySec p {
  margin: 2px 10px 10px 20px;
  font-size: 14px;
  color: #333333;
}
.replySec p a {
  color: #0088cc;
  text-decoration: none;
}
.replySec p a:hover {
  text-decoration: underline;
}
.wwfName {
  font-size: 14px;
  color: #666666;
  font-weight: bold;
  margin-left: 10px;
  position: absolute;
}
.wwfName:hover {
  color: #385f8a;
}
.floor {
  font-size: 14px;
  color: #666666;
  font-weight: bold;
  display: block;
  float: right;
  margin-right: 20px;
  margin-top: 10px;
}
.replyUp a {
  text-decoration: none;
}
</style>


