<template>
  <div class="header">
    <router-link :to="{name:'root'}">
      <img src="../assets/cnodejs_light.svg" alt />
    </router-link>
    <div id="search">
      <i class="iconfont icon-sousuo"></i>
      <input type="text" />
    </div>
    <ul>
      <li>
        <a href>首页</a>
      </li>
      <li>
        <a href="#">新手入门</a>
      </li>
      <li>
        <a href="#">API</a>
      </li>
      <li>
        <a href="#">关于</a>
      </li>
      <li>
        <a href="#">注册</a>
      </li>
      <li>
        <a href="#">登录</a>
      </li>
    </ul>
  </div>
</template>
<script>
export default {
  name: "Header"
};
</script>
<style scoped>
@import "//at.alicdn.com/t/font_1500740_c4ro2rrxn1r.css";
.header {
  background-color: #444444;
  height: 50px;
  position: relative;
}
img {
  position: absolute;
  max-width: 120px;
  top: 8px;
  left: 250px;
}
ul {
  list-style-type: none;
  float: right;
  margin-right: 14%;
  height: 50px;
}
li {
  line-height: 50px;
  display: inline-block;
  margin-left: 25px;
}
li a {
  text-decoration: none;
  color: #cccccc;
  font-size: 14px;
}
li a:hover {
  color: #ffffff;
}
#search {
  position: absolute;
  top: 13px;
  left: 400px;
  width: 230px;
  height: 26px;
  border-radius: 13px;
  background-color: #888888;
}
i {
  color: #444444;
  margin-left: 5px;
  display: inline-block;
  margin-top: 3px;
}
input {
  width: 194px;
  height: 26px;
  background-color: #888888;
  border: 0px;
}
#search:hover {
  background-color: #ffffff;
}
input:hover {
  background-color: #ffffff;
}
</style>


