<template>
  <div class="PostList">
    <!-- 在数据未返回的时候，显示这个正在加载的gif -->
    <div class="loading" v-if="isLoading">
      <img src="../assets/timg.gif" />
    </div>
    <!-- 主题列表 -->
    <div class="posts" v-else>
      <ul>
        <li>
          <div class="topbar">
            <span>全部</span>
            <span>精华</span>
            <span>分享</span>
            <span>问答</span>
            <span>招聘</span>
            <span>客户端测试</span>
          </div>
        </li>
        <li v-for="post in posts" :key="post.id">
          <!-- 头像 -->
          <img :src="post.author.avatar_url" alt />
          <!-- 回复/浏览 -->
          <span class="reply">
            <span class="reply_count">{{post.reply_count}}</span>
            /{{post.visit_count}}
          </span>
          <!-- 帖子的分类 -->
          <span
            :class="[{good:(post.good === true),top:(post.top === true),
          topiclist:(post.good !== true && post.put !== true)}]"
          >
            <span>{{post | tabFormatter}}</span>
          </span>
          <!-- 标题 -->
          <router-link :to="{name:'post_content',params:{id:post.id,name:post.author.loginname}}">
            <span class="tit">{{post.title}}</span>
          </router-link>
          <!-- 最终回复时间 -->
          <span class="last_reply">{{post.last_reply_at | formatDate}}</span>
        </li>
        <li id="last">
          <pagination @handleList="renderList"></pagination>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
import pagination from "./Pagination";
export default {
  name: "Postlist",
  data() {
    return {
      isLoading: false,
      posts: [], //页面列表数组
      postpage: 1
    };
  },
  components: {
    pagination
  },
  methods: {
    getDate() {
      this.$http
        .get("https://cnodejs.org/api/v1/topics", {
          params: {
            page: this.postpage,
            limit: 20
          }
        })
        .then(res => {
          this.isLoading = false; //加载成功去除动画
          this.posts = res.data.data;
        })
        .catch(function(err) {
          console.log(err);
        });
    },
    renderList(value) {
      this.postpage = value;
      this.getDate();
    }
  },
  beforeMount() {
    this.isLoading = true; //在页面加载成功之前获取动画
    this.getDate(); // 在页面加载之前获取数据
  }
};
</script>
<style scoped>
.loading {
  width: 30px;
  height: 30px;
  margin: 0 auto;
  text-align: center;
  padding-top: 300px;
  transform: translateX(-10%);
}
.PostList {
  width: 1100px;
  background-color: #ffffff;
}
ul {
  list-style-type: none;
}
ul li {
  height: 50px;
  border-top: 1px solid #f0f0f0;
  padding-left: 10px;
  line-height: 50px;
  font-size: 14px;
}
ul li:hover {
  background-color: #f5f5f5;
}
#last:hover {
  background-color: #ffffff;
}
ul li:nth-child(1) {
  height: 40px;
  line-height: 40px;
  background-color: #f6f6f6;
  color: #80bd01;
}
.topbar span {
  margin-left: 10px;
  padding: 2px 4px;
  border-radius: 2px;
  cursor: pointer;
}
.topbar span:hover {
  color: #00559d;
}
.topbar span:nth-child(1) {
  background-color: #80bd01;
  color: #ffffff;
}
img {
  width: 30px;
  height: 30px;
  transform: translateY(10px);
}

.topiclist {
  padding: 2px 4px;
  border-radius: 2px;
  background-color: #e5e5e5;
  color: #999999;
  font-size: 12px;
}
.good,
.top {
  padding: 2px 4px;
  border-radius: 2px;
  background-color: #80bd01;
  color: #ffffff;
  font-size: 12px;
}
.last_reply {
  float: right;
  font-size: 12px;
  margin-right: 10px;
  color: #778087;
}
.reply {
  margin: 0 5px;
  font-size: 12px;
  color: #b4b4bb;
}
.reply_count {
  color: #9e78c0;
  font-size: 14px;
}
.tit {
  color: #333333;
}
a {
  text-decoration: none;
}
</style>


