<template>
  <div class="autherinfo">
    <div class="authersummay">
      <div class="slibar">作者</div>
      <router-link
        :to="{
          name:'user_info',
          params:{
            name:userinfo.loginname
          }
          }"
      >
        <img :src="userinfo.avatar_url" />
      </router-link>
      <span>{{userinfo.loginname}}</span>
      <p>积分: {{userinfo.score}}</p>
    </div>
    <div class="recent_topics">
      <div class="slibar">作者最近主题</div>
      <ul>
        <li v-for="list in topcilimitby5">
          <router-link
            :to="{
          name:'post_content',
          params:{id:list.id,name:list.author.loginname}
          }"
          >{{list.title}}</router-link>
        </li>
      </ul>
    </div>
    <div class="recent_replies">
      <div class="slibar">作者最近回复</div>
      <ul>
        <li v-for="list in replieilimitby5">
          <router-link
            :to="{
          name:'post_content',
          params:{id:list.id,name:list.author.loginname}
          }"
          >{{list.title}}</router-link>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
export default {
  name: "SlideBar",
  data() {
    return {
      userinfo: {}
    };
  },
  methods: {
    getDate() {
      this.$http
        .get(`https://cnodejs.org/api/v1/user/${this.$route.params.name}`)
        .then(res => {
          this.isLoading = false; //加载成功去除动画
          this.userinfo = res.data.data;
        })
        .catch(function(err) {
          console.log(err);
        });
    }
  },
  computed: {
    topcilimitby5() {
      if (this.userinfo.recent_topics) {
        return this.userinfo.recent_topics.slice(0, 5);
      }
    },
    replieilimitby5() {
      if (this.userinfo.recent_replies) {
        return this.userinfo.recent_replies.slice(0, 5);
      }
    }
  },

  beforeMount() {
    this.isLoading = true; //在页面加载成功之前获取动画
    this.getDate(); // 在页面加载之前获取数据
  }
};
</script>
<style scoped>
.autherinfo {
  float: right;
  margin-right: 250px;
  font-size: 14px;
}
.authersummay {
  height: 168px;
  width: 290px;
  background-color: #ffffff;
  border-radius: 5px;
  position: relative;
}
.recent_topics,
.recent_replies {
  width: 290px;
  background-color: #ffffff;
  margin-top: 15px;
  border-radius: 5px;
}
.slibar {
  height: 40px;
  background-color: #f6f6f6;
  font-size: 14px;
  line-height: 40px;
  padding-left: 10px;
  color: #444444;
  border-radius: 5px 5px 0 0;
}
.authersummay img {
  width: 50px;
  height: 50px;
  margin: 10px 10px 0 10px;
}
.authersummay span {
  color: #778087;
  position: absolute;
  top: 65px;
  left: 80px;
}
.authersummay p{
  margin: 10px 0 0 10px;
  color: #333333;
}
ul {
  padding: 12px;
  list-style-type: none;
}
ul li {
  margin-bottom: 10px;
}
ul li a {
  text-decoration: none;
  color: #778087;
}
ul li a:hover {
  text-decoration: underline;
  color: #005580;
}
</style>


